  data () {
    return {
      value: true,
      value2: false
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>loading</h3>
<p>laoding的颜色取open时的背景色</p>
<mku-example>
        
        <template slot="source"><element-demo5 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-switch loading /&gt;
    &lt;mku-switch loading :value=&quot;true&quot;/&gt;
    &lt;mku-switch loading :value=&quot;true&quot; active-color=&quot;#f00&quot;/&gt;
  &lt;/div&gt;
&lt;/template&gt;
</code></pre></template></mku-example><h3>自定义<code>true</code>和<code>false</code>的值</h3>
<mku-example>
        
        <template slot="source"><element-demo6 /></template>
        <template slot="highlight"><pre v-pre><code class="html">&lt;template&gt;
  &lt;div&gt;
    &lt;mku-switch
      v-model=&quot;value&quot;
      :true-value=&quot;1&quot;
      :false-value=&quot;0&quot;
      @change=&quot;handleChange&quot;
    /&gt;
  &lt;/div&gt;
&lt;/template&gt;

&lt;script&gt;
export default {
  data () {
    return {
      value: 1
    }
  },
  methods: {
    handleChange (val) {
      this.$message.success(`switch状态:${val}`)
    }
  }
}
&lt;/script&gt;
</code></pre></template></mku-example><h3>Switch Attributes</h3>
<table>
<thead>
<tr>
<th style="text-align:left">参数</th>
<th style="text-align:left">说明</th>
<th style="text-align:left">类型</th>
<th style="text-align:left">可选值</th>
<th style="text-align:left">默认值</th>
<th style="text-align:left">必填</th>
</tr>
</thead>
<tbody>
<tr>
<td style="text-align:left">value</td>
<td style="text-align:left">绑定的值，使用<code>v-model</code>进行双向数据绑定</td>
<td style="text-align:left"><code>String | Number | Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">-</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">width</td>
<td style="text-align:left">自定义switch的宽度</td>
<td style="text-align:left"><code>Number</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left">50 （单位px）</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">size</td>
<td style="text-align:left">switch尺寸</td>
<td style="text-align:left">String</td>
<td style="text-align:left"><code>large | default | small</code></td>
<td style="text-align:left"><code>default</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">active-color</td>
<td style="text-align:left">open时的背景色</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">主题蓝</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">inactive-color</td>
<td style="text-align:left">close时的背景色</td>
<td style="text-align:left">String</td>
<td style="text-align:left">-</td>
<td style="text-align:left">主题灰</td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">true-value</td>
<td style="text-align:left">自定义true的值</td>
<td style="text-align:left"><code>String | Number | Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>true</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">false-value</td>
<td style="text-align:left">自定义false的值</td>
<td style="text-align:left"><code>String | Number | Boolean</code></td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">disabled</td>
<td style="text-align:left">禁用switch</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
<tr>
<td style="text-align:left">loading</td>
<td style="text-align:left">展示loading效果，此时点击不会更改状态</td>
<td style="text-align:left">Boolean</td>
<td style="text-align:left">-</td>
<td style="text-align:left"><code>false</code></td>
<td style="text-align:left">否</td>
</tr>
</tbody>
</table>
<h3>Switch Events</h3>
<table>
<thead>
<tr>
<th style="text-align:left">事件名</th>
